export default {
	data() {
		return {
			tittleCount:"0",
			jobInfo:"0",
			sex: 3,
			place:"西乡塘",
			tittle:"",
			content:"",
			placeDetail:"",
			people:"",
			jobData:{},
			type:""
		}
	},
	methods: {
		getPlaceDetail(e){
			this.placeDetail = e.detail.value;
		},
		getPeople(e){
			this.people = e.detail.value;
		},
		getTittleCount(e){
			this.tittleCount = e.detail.value.length;
			this.tittle = e.detail.value;
		},
		getJobInfo(e){
			this.jobInfo = e.detail.value.length;
			this.content = e.detail.value;
		},
		next(e){
			if(this.tittle==""){
				uni.showToast({
					icon:"none",
					title:"请输入兼职标题"
				})
				return
			}
			if(this.content==""){
				uni.showToast({
					icon:"none",
					title:"请输入职位描述"
				})
				return
			}
			if(this.jobInfo<20){
				uni.showToast({
					icon:"none",
					title:"职位描述需填写20~500字"
				})
				return
			}
			if(this.people==""){
				uni.showToast({
					icon:"none",
					title:"请输入招聘人数"
				})
				return
			}
			if(this.placeDetail==""){
				uni.showToast({
					icon:"none",
					title:"请输入南宁市内详细地址"
				})
				return
			}
			if(this.place=="请选择"){
				uni.showToast({
					icon:"none",
					title:"请选择工作地点"
				})
				return
			}
			var _this = this
			uni.getStorage({
			    key: 'jobData',
			    success: function (res) {
			        console.log(res.data);
					_this.jobData = res.data
					_this.jobData.sex = _this.sex
					_this.jobData.tittle = _this.tittle
					_this.jobData.content = _this.content
					_this.jobData.recruitNum = parseInt(_this.people)
					_this.jobData.detailPlace = _this.placeDetail
					_this.jobData.place = _this.place
					console.log(_this.jobData);
					uni.setStorage({
					    key: 'jobData',
					    data: _this.jobData,
					    success: function () {　　　　　　　　　
					        // 存储成功后跳转第三步
					        uni.navigateTo({
					             url:"./job-release-three"
					        })
					    }
					})  
			    }
			});
			// console.log(this.jobData);		
			
		}
	}
}
